<template>
  <div class="request">
    <div class="request-content">
      <div class="content-title">
        <i class="font_family iconfont icon-kongzhi"></i>Control Panel
      </div>
      <div :class="['content-conditions',conditionsStatus?'is-expand':'']">
        <span class="conditions-status" @click="conditionsStatus = !conditionsStatus">
          执行
          <i :class="[conditionsStatus?'el-icon-arrow-up':'el-icon-arrow-down']"></i>
        </span>
        <Search v-show="conditionsStatus" />
      </div>
      <div class="content-table">
        <TableItem
          :tableOptions="tableOptions"
          :tableColumns="tableColumns"
          :tableDatas="tableDatas"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Search from "@/components/Search.vue";
import TableItem from "@/components/Table.vue";

export default {
  name: "RequestPage",
  components: {
    Search,
    TableItem
  },
  data() {
    return {
      conditionsStatus: false,
      tableOptions: {
        height: "650px"
      },
      tableColumns: [
        {
          label: "Equipment",
          prop: "equipment"
        },
        {
          label: "Instruction ID",
          prop: "insid"
        },
        {
          label: "Requestor",
          prop: "requestor"
        },
        {
          label: "Instruction typeCat",
          prop: "intype"
        },
        {
          label: "Status",
          prop: "status"
        },
        {
          label: "Log",
          prop: "log"
        },
        {
          label: "Process",
          prop: "process",
          render: ({ process }) => {
            return `<span>${process}</span>`;
          }
        },
        {
          label: "Down time",
          prop: "dotime"
        }
      ],
      tableDatas: []
    };
  },
  watch: {
    conditionsStatus(newValue, oldValue) {
      this.tableOptions.height = newValue ? "518px" : "650px";
    }
  },
  mounted() {
    for (let i = 0; i < 20; i++) {
      this.tableDatas.push({
        equipment: `51${i}`,
        insid: `SWC0${i}`,
        requestor: `缝制`,
        intype: `10${i}`,
        status: `6${i}`,
        log: `缝制`,
        process: `工位1 --> 工位2`,
        dotime: `6${i}`
      });
    }
  },
  methods: {}
};
</script>

<style lang="scss">
.request {
  position: relative;
  width: 1440px;
  height: 826px;
  padding: 32px 39px 35px 37px;
  background: #eaedf1;

  .request-content {
    background: #fff;

    .content-conditions {
      position: relative;
      height: 0px;
      border-top: 1px solid #d8dbdf;
      z-index: 9;

      .conditions-status {
        padding: 0 5px;
        // width: 30px;
        // height: 20px;
        background: #fff;
        display: inline-block;
        font-size: 14px;
        color: #002b5e;
        position: absolute;
        top: -7px;
        right: 27px;
        cursor: pointer;
      }
    }

    .is-expand {
      height: 132px;
    }

    .content-table {
      padding: 0 50px 29px 50px;
    }
  }
}
</style>